<template>
    <div class="globeContainer">
        <Globe v-if="activeGlobeView === 'globe'" :globeMarkersToDisplay="globeMarkersToDisplay" />
        <div class="wrapper" :class="{isMap}">
            <Map v-if="activeGlobeView === 'map'" :markersToDisplay="globeMarkersToDisplay" />
            <GlobeControls />
        </div>
    </div>
</template>

<script>
    import { createNamespacedHelpers } from 'vuex';
    const { mapActions, mapState } = createNamespacedHelpers('globeModule');

    import Map from './Map';
    import Globe from './Globe';
    import GlobeControls from './GlobeControls';

    export default {
        components: {
            Map,
            Globe,
            GlobeControls
        },
        computed: {
            ...mapState(['globeMarkersToDisplay', 'activeGlobeView']),
            isMap: function () {
                return this.activeGlobeView === 'map'
            }
        },
        methods: {
            ...mapActions({
                toggleLoading: 'toggleLoading'
            })
        }
    }
</script>

<style scoped lang="scss">
    @import '~include-media/dist/_include-media';

    .wrapper {
     position: absolute;
     display: flex;
     padding-top: 200px;
     right: 100px;
     top: 0;

        &:not(.isMap) {
            @include media('<1450px', 'screen') {
                & {
                    width: 100%;
                    right: 0;
                }
            }
        }

        &.isMap {
            width: 80%;
        }
    }
</style>

