<template>
    <div class="chartPageContainer">
        <div class="chartsControls">
            <div class="sliderContainer">
                <v-range-slider
                    class="slider"
                    v-model="chartYearsRangeSlider"
                    thumb-label="always"
                    :thumb-size="28"
                    :min="1929"
                    :max="2014"></v-range-slider>
            <v-btn
                    class="ma-2 updateChartsButton"
                    outlined
                    @click="updateCharts">
                apply filter
            </v-btn>
            </div>
    </div>
        <div class="chartsContainer">
            <StockChart />
            <div class="pieChartsContainer">
                <PieChart
                    v-for="(singlePieChartData) in pieFormattedChartsData"
                    :pieChartData="singlePieChartData"
                    :chartName="singlePieChartData.chartName"
                    :key="singlePieChartData.chartName" />
            </div>

        </div>
    </div>
</template>

<script>
    import { map } from 'lodash';
    import { createNamespacedHelpers } from 'vuex';
    const { mapState, mapActions } = createNamespacedHelpers('statisticModule');

    import StockChart from './StockChart'
    import PieChart from './PieChart'

    const monthsMap = {
        '1': 'January',
        '2': 'February',
        '3': 'March',
        '4': 'April',
        '5': 'May',
        '6': 'June',
        '7': 'July',
        '8': 'August',
        '9': 'September',
        '10': 'October',
        '11': 'November',
        '12': 'December'
    };


    export default {
        props: {},
        components: {
            StockChart,
            PieChart
        },
        data: () => ({
            pieFormattedChartsData: [],
            chartYearsRangeSlider: [1999, 2014]
        }),
        computed: {
            ...mapState(['pieChartDataTypes', 'pieChartsData'])
        },
        created() {
            this.updateCharts();
        },
        watch: {
            pieChartsData () {
                this.pieFormattedChartsData = map(this.pieChartsData, (data, key) => ({
                    data: map(data, ({ name, value }, index) => ({ name: key === 'month' ? monthsMap[index] : name , y: value })),
                    chartName: key
                }))
            },
            chartYearsRangeSlider() {
                this.setChartYearsRangeSlider(this.chartYearsRangeSlider)
            }
        },
        methods: {
            ...mapActions({
                updateCharts: 'updateCharts',
                setChartYearsRangeSlider: 'setChartYearsRangeSlider'
            }),
            getSinglePieChartData(singlePieChartData, chartType) {
                return map(singlePieChartData, ({ name, value }) => ({
                    name: chartType === 'month' ? monthsMap[chartType] : name,
                    y: value
                }))
            }
        }
    }
</script>

