<template>
    <div class="controlsContainer">
        <div class="filterControlsContainer">
        <div class="groupControlContainer">
            <span class="controlTitle">Grouped by</span>
            <v-select
                    class="groupControl"
                    outlined
                    dark
                    v-model="groupedByRegionId"
                    :items="regions"
                    menu-props="auto"
                    filled
                    dense
                    item-value="value"
                    label="Grouped by"
                    single-line
            ></v-select>
        </div>
        <div class="control">
            <span class="controlTitle">Year</span>
            <v-range-slider
                v-model="yearsRangeSlider"
               thumb-label="always"
                :thumb-size="28"
                :min="yearsRange.start"
                :max="yearsRange.stop"></v-range-slider>
        </div>
         <div class="control">
             <span class="controlTitle">Month</span>
             <v-range-slider
            v-model="monthsRangeSlider"
           thumb-label="always"
            :thumb-size="24"
            :min="monthsMinMax.min"
            :max="monthsMinMax.max"></v-range-slider>
        </div>
         <div class="control">
             <span class="controlTitle">Day</span>
             <v-range-slider
            v-model="daysRangeSlider"
           thumb-label="always"
            :thumb-size="24"
            :min="daysMinMax.min"
            :max="daysMinMax.max"></v-range-slider>
        </div>
        <div class="control">
            <v-btn
                    class="ma-2 applyFilterButton"
                    outlined
                    @click="applyFilter">
                apply filter
            </v-btn>
        </div>
            <ToggleGlobeControls />
    </div>
    </div>
</template>

<script>
    import { createNamespacedHelpers } from 'vuex';
    const { mapActions, mapState } = createNamespacedHelpers('globeModule');

    import ToggleGlobeControls from './ToggleGlobeViewControls';

    const CONTROLS_YEARS_MIN_MAX = {
        min: 2012,
        max: 2014
    };
    const CONTROLS_MONTHS_MIN_MAX = {
        min: 1,
        max: 12
    };
    const CONTROLS_DAYS_MIN_MAX = {
        min: 1,
        max: 31
    };

    const REGIONS = [
        {
        text: 'Not grouped',
        value: '0'
        }, {
        text: 'City',
        value: '1'
    },{
        text: 'State/province',
        value: '2'
    }, {
        text: 'Country',
        value: '3'
    }];

    export default {
        data: () => ({
            regions: REGIONS,
            groupedByRegionId: null,
            yearsRangeSlider: null,
            monthsRangeSlider: null,
            daysRangeSlider: null,
            monthsMinMax: {
                min: null,
                max: null
            },
            daysMinMax: {
                min: null,
                max: null
            }
        }),
        components: {
            ToggleGlobeControls
        },
        watch: {
            min () {
                this.setYearsRange();
            },
            max () {
                this.setYearsRange();
            }
        },
        computed: {
            ...mapState(['yearsRange'])
        },
        methods: {
            ...mapActions({
                setGlobeData: 'setGlobeData'
            }),
            setYearsRange () {
                this.yearsRangeSlider = [this.minYear, this.maxYear];
            },
            async applyFilter () {
                const years = this.getDatePartItems(this.yearsRangeSlider);
                const months = this.getDatePartItems(this.monthsRangeSlider);
                const days = this.getDatePartItems(this.daysRangeSlider);

                this.setGlobeData({ years, months, days, groupedByRegionId: this.groupedByRegionId })
            },
            getDatePartItems (range) {
                const list = [];

                for (let i = range[0]; i <= range[1]; i++) {
                    list.push(i);
                }

                return list;
            },
        },
        created () {
            this.groupedByRegionId = REGIONS[3].value;
            const { min: yearMin, max: yearMax } = CONTROLS_YEARS_MIN_MAX;
            const { min: monthMin, max: monthMax } = CONTROLS_MONTHS_MIN_MAX;
            const { min: daysMin,  max: daysMax, } = CONTROLS_DAYS_MIN_MAX;

            this.yearsRangeSlider = [yearMin, yearMax];
            this.monthsRangeSlider = [monthMin, monthMax];
            this.daysRangeSlider = [daysMin, daysMax];
            this.monthsMinMax =  { min: monthMin, max: monthMax };
            this.daysMinMax =  { min: daysMin, max: daysMax };

            this.applyFilter();
        }
    }
</script>

