<template>
    <div class="pieChartContainer" :id="chartName"></div>
</template>

<script>
    import { isNil } from 'lodash';
    import Highcharts from 'highcharts'
    import { pieChartTheme } from '../thems'

    Highcharts.theme = pieChartTheme(Highcharts);
    Highcharts.setOptions(Highcharts.theme);

    export default {
        props: {
            chartName: {
                type: String,
                required: true
            },
            pieChartData: {
                type: Object,
                required: true
            }
        },
        data: () => ({
            chartData: []
        }),
        watch: {
            pieChartData() {
                // eslint-disable-next-line no-console
                this.chartData = this.pieChartData.data;
                this.initializePieChart();
            }
        },
        methods: {
            pieChartUnicId () {
                return`pieChartContainer-${this.chartName}`
            },
            initializePieChart() {
                const highchartsOptions = {
                    chart: {
                        plotBackgroundColor: null,
                        plotBorderWidth: null,
                        plotShadow: false,
                        type: 'pie',
                        renderTo: this.chartName,
                    },
                    title: {
                        text: `Statistic by ${this.chartName} frequency`
                    },
                    tooltip: {
                        pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
                    },
                    plotOptions: {
                        pie: {
                            allowPointSelect: true,
                            cursor: 'pointer',
                            dataLabels: {
                                enabled: true,
                                format: '<b>{point.name}</b>: {point.percentage:.1f} %'
                            }
                        }
                    },
                    series: [{
                        name: 'Brands',
                        colorByPoint: true,
                        data: this.chartData
                    }]
                };

                this.chart = new Highcharts.Chart(highchartsOptions);
            },
            redraw(){
                if(!isNil(this.chart.series[0])) {
                    this.chart.series[0].setData(this.chartData);
                }
            },
        }
    }
</script>

<style scoped lang="scss">
    .pieChartContainer {
        width: 50%;
        height: 100%;
    }
</style>