<template>
        <div class="stockChartContainer">
            <div class="chartContainer" id="stockChartContainer"></div>
            <div class="bottomGroupControls">
                <v-btn outlined
                       class="bottomGroupToggleChartType"
                       @click="toggleChartType()">
                    {{isAreaCurrentChartType ? 'Switch to Column view' : 'Switch to Area view'}}
                </v-btn>
                <div class="chartTypesOfGroupingControls">
                    <v-btn v-for="groupType in groupChartTypes"
                           :key="groupType"
                           outlined
                           class="bottomGroupControlsButton"
                           :class="{active: groupType === activeChartType}"
                           @click="changeActiveChartType(groupType)">{{groupType}}</v-btn>
                </div>
                <v-btn outlined
                       class="resetZomButton"
                       @click="resetZomButton()">reset zoom</v-btn>
            </div>
        </div>
</template>

<script>
    import { map } from 'lodash';
    import Highcharts from 'highcharts'
    import { stockChartTheme } from '../thems'

    import { createNamespacedHelpers } from 'vuex';
    const { mapActions, mapState } = createNamespacedHelpers('statisticModule');

    Highcharts.theme = stockChartTheme(Highcharts);
    Highcharts.setOptions(Highcharts.theme);

    const GROUP_CHART_TYPES = ['day', 'month', 'year'];
    const CHART_TYPES = {
        AREA: 'area',
        COLUMN: 'column'
    };

    export default {
        data: () => ({
            chartData: [],
            isAreaCurrentChartType: true,
            groupChartTypes: GROUP_CHART_TYPES,
            activeChartType: GROUP_CHART_TYPES[1],
        }),
        watch: {
            activeChartType() {
                this.redraw();
            },
            isAreaCurrentChartType() {
                this.redraw();
            },
            stockChartData() {
                this.redraw();
            }
        },
        computed: {
            ...mapState(['stockChartData'])
        },
        mounted() {
            const highchartsOptions = {
                chart: {
                    type: 'area',
                    zoomType: 'x',
                    renderTo: 'stockChartContainer',
                    resetZoomButton: {
                        theme: {
                            display: 'none'
                        }
                    }
                },
                title: {
                    text: 'Ufo frequency chart'
                },
                xAxis: {
                    type: 'datetime'
                },
                yAxis: {
                    title: {
                        text: 'Ufo amount'
                    }
                },
                legend: {
                    enabled: false
                },
                series: [{
                    name: 'UFO amount',
                    type: 'area',
                    data: [],
                    color: '#6fcd98'
                }],
                plotOptions: {
                    area: {
                        fillColor: {
                            linearGradient: {
                                x1: 0,
                                y1: 0,
                                x2: 0,
                                y2: 1
                            },
                            stops: [
                                [0, Highcharts.getOptions().colors[0]],
                                [1, Highcharts.Color(Highcharts.getOptions().colors[0]).setOpacity(0).get('rgba')]
                            ]
                        },
                        marker: {
                            radius: 2,
                            enabled: false
                        },
                        lineWidth: 1,
                        states: {
                            hover: {
                                lineWidth: 1
                            }
                        },
                        threshold: null
                    }
                },
            };

            this.chart = new Highcharts.StockChart(highchartsOptions);
            this.updateCharts();
            this.redraw();
        },
        methods: {
            ...mapActions({
                updateCharts: 'updateCharts'
            }),
            redraw(){
                this.chart.series[0].update({
                    data: this.getGroupedUfoItems(),
                    type: this.isAreaCurrentChartType ? CHART_TYPES.AREA : CHART_TYPES.COLUMN
                });
            },
            getGroupedUfoItems () {
                const ufoItems = map(this.stockChartData[this.activeChartType], ({ milliseconds_date, amount }) => [milliseconds_date, amount]);
                return ufoItems;
            },
            changeActiveChartType(newChartType) {
                this.activeChartType = newChartType
            },
            toggleChartType() {
                this.isAreaCurrentChartType = !this.isAreaCurrentChartType;
            },
            resetZomButton() {
                this.chart.zoomOut()
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../common/styles/constants";

    .stockChartContainer {
        .chartContainer {
            height: 100%;
        }

        .bottomGroupControls {
            padding: 10px 0;
            display: flex;
            justify-content: space-between;

            .resetZomButton,
            .bottomGroupToggleChartType,
            .chartTypesOfGroupingControls .bottomGroupControlsButton {
                margin: 0 10px;
                text-transform: uppercase;
                font-size: 14px;
                padding: 0 5px;
                height: 25px;
                color: $white;
                border-radius: 0;
                border-color: transparent;

                &.bottomGroupToggleChartType {
                    color: $blue;
                }
            }

            .chartTypesOfGroupingControls {
                .bottomGroupControlsButton {
                    &.active {
                        color: $green;
                        border-color: $green;
                    }
                }

            }
        }
    }
</style>