<template>
  <div class="rootContainer">
    <AppAnimatedBackground />
    <div class="center">
        <Header @changeActiveTab="changeActiveTab" :activeTabId="activeTabId" :tabs="tabs" />
        <GlobeContainer v-if="activeTabId === tabs[1]" />
        <UfoChartsStatistic v-else />
    </div>
    <v-overlay :value="isLoading">
      <v-progress-circular
              indeterminate
              size="64" />
    </v-overlay>
  </div>
</template>

<script>
  import { isNil } from 'lodash'
  import Header from './Header';
  import UfoChartsStatistic from './UfoChartsStatistic';
  import AppAnimatedBackground from './AppAnimatedBackground';
  import GlobeContainer from './globe/GlobeContainer';

  import { createNamespacedHelpers } from 'vuex';
  const { mapState } = createNamespacedHelpers('commonModule');

  const TABS = ['globe', 'chart statistic'];

  export default {
    components: {
      Header,
      GlobeContainer,
      UfoChartsStatistic,
      AppAnimatedBackground
    },
    data: () => ({
      serverApi: null,
      tabs: TABS,
      activeTabId: TABS[0]
    }),
      created () {
          const { VUE_APP_FD_SERVER_URL } = process.env;
          this.serverApi = isNil(VUE_APP_FD_SERVER_URL) ? null : `${process.env.VUE_APP_FD_SERVER_URL}`;
      },
      computed: {
          ...mapState(['isLoading'])
      },
      methods: {
          changeActiveTab(newActiveTabId) {
              this.activeTabId = newActiveTabId;
          }
      }
  }
</script>

<style scoped lang="scss">
  .rootContainer {
    display: flex;
    justify-content: center;
    height: 100vh;

    .center {
      display: flex;
      z-index: 1;
      width: 100%;
      justify-content: center;
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      position: absolute;
    }
  }
</style>
