<template>
    <div class="mapContainer">
        <GmapMap
                :center="{ lat: 48.1750554, lng: 1.4843474 }"
                :zoom="4"
                class="map"
                :options="{
          streetViewControl: false,
        }"
                map-type-id="terrain">
            <GmapMarker
                    v-for="marker in markersToDisplay"
                    :key="marker.date_documented + Math.random()"
                    :position="getPosition(marker)" />
        </GmapMap>
    </div>
</template>

<script>
    import { toNumber } from 'lodash';

    import { createNamespacedHelpers } from 'vuex';
    const { mapState } = createNamespacedHelpers('globeModule');

    export default {
        props: {
            markersToDisplay: {
                type: Array,
                required: true
            }
        },
        computed: {
            ...mapState(['globeMarkersToDisplay'])
        },
        methods: {
            getPosition ({ longitude, latitude }) {
                return {
                    lat: toNumber(latitude),
                    lng: toNumber(longitude)
                }
            }
        }
    };
</script>

<style scoped lang="scss">
    .mapContainer {
        flex: 1;
        height: 550px;

        .map {
            width: 100%;
            height: 100%;
        }
    }
</style>
