import { TOGGLE_LOADING } from './common.actions';

export default {
  namespaced: true,
  state: {
    isLoading: false
  },
  mutations:{
    TOGGLE_LOADING (state) {
      state.isLoading = !state.isLoading;
    }
  },
  actions: {
    toggleLoading({ commit }) {
      commit(TOGGLE_LOADING);
    },
  }
};
