import { map } from 'lodash';
import { SET_STATISTIC_CHART_DATA, SET_YEARS_RANGE_SLIDER } from './statistic.actions';

import usoService from '../../services/ufoService'

export default {
  namespaced: true,
  state: {
    pieChartsData: null,
    pieChartDataTypes: [],
    stockChartData: null,
    yearsRange: { start: 1929, stop: 2014 },
    yearsRangeSlider: [1999, 2014]
  },
  mutations:{
    SET_STATISTIC_CHART_DATA (state, { pieChartsData, stockChartData }) {
      state.pieChartsData = pieChartsData;
      state.stockChartData = stockChartData;
      state.pieChartDataTypes = map(pieChartsData, (value, type) => type);
    },
    SET_YEARS_RANGE_SLIDER (state, payload) {
      state.yearsRangeSlider = payload;
    }
  },
  actions: {
    updateCharts:  async ({ commit, state }) => {
      const start = state.yearsRangeSlider[0];
      const stop = state.yearsRangeSlider[1];

      const { pie_chart_data, stock_chart_data } = await usoService.fetchStatisticChartData(start, stop);

      commit(SET_STATISTIC_CHART_DATA, { pieChartsData: pie_chart_data, stockChartData: stock_chart_data });
    },
    setChartYearsRangeSlider ({ commit }, payload) {
      commit(SET_YEARS_RANGE_SLIDER, payload);
    }
  }
};
